.header__logo {
  max-width: 142px;
  width: 100%;
  min-height: 33px;
  background-image: url(../../../images/logo.svg);
  background-size: 142px;
  background-repeat: no-repeat;
}

@media screen and (max-width: 768px) {
  .header__logo {
    max-width: 103.74px;
    min-height: 24.4px;
    margin: 27px 0 32px 27px;
    background-size: 102px;
    background-repeat: no-repeat;
  }
}
